import { FaRegEyeSlash, FaRegEye } from "react-icons/fa6";
import { cn } from "../../utils/custom_functions";
import { Button } from "../reusable_components/custom_button";
import { useForm } from "react-hook-form";
import { useMutation } from "react-query";
import { registerFunction } from "../../api/api";
import { useState } from "react";
import { ErrorAlert, SuccessAlert } from "../reusable_components/alerts/alerts";
import { ThreeDots } from "react-loader-spinner";
import { ROUTE_LINKS } from "../../utils/route_links";
import { useTimeout } from "../../utils/hooks/use_time_out";

export default function Register() {
  const [isPasswordVisible, setIspasswordVisible] = useState(false);
  const [disableUserInterface, setDisableUserInterface] = useState(false);
  const { set } = useTimeout(
    () => (window.location.href = ROUTE_LINKS.sigin),
    5000
  );
  //
  const {
    register,
    handleSubmit,
    formState: { isLoading: isFormLoading, errors },
  } = useForm();

  const {
    data,
    isLoading: isRequestLoading,
    isSuccess,
    mutateAsync: registerAsync,
    isError,
    error,
  } = useMutation("register", {
    mutationFn: registerFunction,
    onSuccess: (data) => {
      set();
      setDisableUserInterface((p) => true);
    },
    onError: (e) => setDisableUserInterface((p) => false),
    onMutate: (data) => setDisableUserInterface((p) => true),
  });

  // define
  const onSubmit = async (data) => {
    try {
      // data["is_customer"] = true;
      await registerAsync(data);
    } catch (error) {}
    // console.log(data);
  };
  //return
  return (
    <main className="max-w-xl mx-auto p-6 my-6 text-black bg-white">
      <div className="rounded-lg p-6 shadow-sm border border-gray-200">
        <h2 className="text-start text-black pb-6 px-2 font-extrabold text-3xl">
          Welcome
          <p className="text-sm font-normal text-slate-600 pt-2">
            Trust Matols logistics services to transport your items safely.
            Already have and account?{" "}
            <a href={ROUTE_LINKS.sigin} className="text-yellow-400 font-bold">
              Sign In
            </a>
            .
          </p>
        </h2>
        <form
          className="px-2"
          autoComplete="off"
          onSubmit={handleSubmit(onSubmit)}
        >
          <div>
            <input
              {...register("email", {
                required: "Email required",
                disabled: disableUserInterface,
              })}
              type="email"
              name="email"
              autoComplete="email"
              placeholder="Enter email address"
              className={cn(
                "text-sm text-slate-800  w-full relative shadow-sm block rounded-t-lg border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-50 disabled:pointer-events-none",
                {}
              )}
            />
            <input
              {...register("user_first_name", {
                required: "User name required",
                disabled: disableUserInterface,
              })}
              id="user_first_name"
              type="text"
              name="user_first_name"
              placeholder="Enter first name"
              className={cn(
                "text-sm text-slate-800  w-full relative shadow-sm block border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-50 disabled:pointer-events-none",
                {}
              )}
            />
            <input
              {...register("user_surname", {
                required: "User surname required",
                disabled: disableUserInterface,
              })}
              type="text"
              name="user_surname"
              placeholder="Enter surname"
              autoComplete="new-off"
              className={cn(
                "text-sm text-slate-800  w-full relative shadow-sm block border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-50 disabled:pointer-events-none",
                {}
              )}
            />
            <input
              {...register("user_contact_number", {
                required: "User contact number required",
                disabled: disableUserInterface,
                maxLength: {
                  value: 10,
                  message: "Phone number cannot be more than 10 digits",
                },
                minLength: {
                  value: 10,
                  message: "Phone number cannot be less than 10 digits",
                },
              })}
              type="tel"
              id="user_contact_number"
              name="user_contact_number"
              placeholder="Enter phone number"
              className={cn(
                "text-sm text-slate-800  w-full relative shadow-sm block border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-50 disabled:pointer-events-none",
                {}
              )}
            />
            <div className="relative">
              <input
                {...register("password", {
                  required: "Password required",
                  disabled: disableUserInterface,
                })}
                type={isPasswordVisible ? "text" : "password"}
                name="password"
                placeholder="Enter password"
                autoComplete="new-password"
                className={cn(
                  "text-sm pe-9 text-slate-800 w-full shadow-sm relative block rounded-b-lg border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-50 disabled:pointer-events-none",
                  {}
                )}
              />
              <div className="absolute inset-y-0 right-0 z-10 px-3 flex justify-center items-center">
                <div onClick={() => setIspasswordVisible((r) => !r)}>
                  {!isPasswordVisible ? (
                    <FaRegEyeSlash
                      className="text-yellow-400 cursor-pointer"
                      size={20}
                    />
                  ) : (
                    <FaRegEye
                      className="text-yellow-400 cursor-pointer"
                      size={20}
                    />
                  )}
                </div>
              </div>
            </div>

            <input
              {...register("is_customer")}
              id="is_customer"
              name="is_customer"
              type="checkbox"
              defaultChecked
              className="hidden"
            />

            <div className="flex items-center justify-start my-4 gap-2 text-sm">
              <input
                {...register("is_terms_cond_accept", {
                  required: "Accept our terms and privacy policy",
                  disabled: disableUserInterface,
                })}
                id="is_terms_cond_accept"
                aria-label="is_terms_cond_accept"
                name="is_terms_cond_accept"
                type="checkbox"
                className="rounded-sm border-gray-300 text-yellow-400 focus:ring-yellow-400 disabled:opacity-50 disabled:pointer-events-none"
              />
              <label htmlFor="termsandprivacy">
                I agree to the{" "}
                <a href="/" className="font-bold text-yellow-400">
                  Terms
                </a>{" "}
                and{" "}
                <a href="/" className="font-bold text-yellow-400">
                  Privacy Policy
                </a>
                .
              </label>
            </div>

            <div className="pb-4 text-sm italic text-red-500 ">
              {errors.email && <p>{errors.email.message}</p>}
              {errors.user_first_name && (
                <p>{errors.user_first_name.message}</p>
              )}
              {errors.user_surname && <p>{errors.user_surname.message}</p>}
              {errors.user_contact_number && (
                <p>{errors.user_contact_number.message}</p>
              )}
              {errors.password && <p>{errors.password.message}</p>}
              {errors.is_terms_cond_accept && (
                <p>{errors.is_terms_cond_accept.message}</p>
              )}
            </div>
            {/*Network error and success alerts*/}
            {isError && (
              <ErrorAlert
                className="mb-2"
                secondaryText={`${error.response.data.email} ${error.message}`}
              />
            )}
            {isSuccess && (
              <SuccessAlert
                secondaryText={`${data.message}. Navigate to sign in page and sigin`}
                className="mb-4"
              />
            )}
            {/**/}
            <Button
              className="rounded-lg text-sm shadow-sm"
              type="submit"
              disabled={disableUserInterface}
            >
              {isRequestLoading || isFormLoading ? (
                <div className="w-full h-full flex justify-center items-center">
                  <ThreeDots visible width="40" height="20" color="#000" />
                </div>
              ) : (
                "Register new account"
              )}
            </Button>
          </div>
        </form>
      </div>
    </main>
  );
}
